.story-viewer-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.viewer-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 2px solid #e0e0e0;

  h2 {
    margin: 0;
    font-size: 1.5rem;
    color: #333;
  }

  .actions {
    display: flex;
    gap: 5px;
  }
}

.viewer-content {
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.date-badge {
  font-size: 1.2rem;
  font-weight: 600;
  color: #667eea;
  margin-bottom: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 12px;
  text-align: center;
}

.story-meta {
  display: flex;
  gap: 15px;
  margin-bottom: 25px;
  flex-wrap: wrap;

  .meta-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 15px;
    background: white;
    border: 2px solid #667eea;
    border-radius: 20px;
    color: #667eea;
    font-weight: 500;

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }
  }
}

.story-card {
  margin-bottom: 30px;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);

  mat-card-header {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 20px;
    border-radius: 16px 16px 0 0;
    margin: -16px -16px 20px -16px;

    ::ng-deep mat-card-title {
      color: white;
      margin: 0;
      font-size: 1.3rem;
    }
  }

  .story-content {
    font-size: 1.1rem;
    line-height: 1.8;
    color: #333;
    white-space: pre-wrap;
    word-break: keep-all;
  }
}

.additional-info {
  display: grid;
  gap: 20px;

  .info-section {
    padding: 20px;
    background: #f8f9fa;
    border-radius: 12px;
    border-left: 4px solid #667eea;

    h3 {
      margin: 0 0 10px 0;
      color: #667eea;
      font-size: 1.1rem;
    }

    p {
      margin: 0;
      line-height: 1.6;
      color: #555;
    }
  }
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100px 20px;
  gap: 20px;

  p {
    color: #666;
    font-size: 1.1rem;
  }
}

@media (max-width: 768px) {
  .story-viewer-container {
    padding: 15px;
  }

  .viewer-header h2 {
    font-size: 1.2rem;
  }

  .story-card .story-content {
    font-size: 1rem;
  }
}
